<template>
  <div style="width:100%;height:100%;">
    <div id="cesiumContainer">
        <div class="textboxMain">
                <div class="divH">行驶方向偏移量：<span id="heading"></span></div>
                <div class="divH" hidden>俯视偏移量：<span id="pitch"></span></div>
                <div class="divH">侧身偏移量：<span id="roll"></span></div>
                <div class="divH">当前行驶速度：<span id="speed"></span></div>
                <div class="divH" style="color: rgb(75, 165, 243);">切换飞行模式 <input type="checkbox"  id="fromBehind"></div>

                <div class="divHh">
                    <h3>操作说明：</h3>
                    <p>1、键盘左、右键操控飞机航驶方向</p>
                    <p>2、shift+键盘上、下键操控飞机行驶速度</p>
                </div>
                
        </div>
    </div>
  </div>
</template>

<script>
export default {
    name:'cesiumViewer',
    data() {
        return {
            viewer:{}
        }
    },
    computed: {

    },
    watch:{

    },
    mounted(){
        Cesium.Ion.defaultAccessToken = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiI3NjRjNGFjNy1jNDM3LTQzMTktODVlYS05YmFmOTAxYjk5MWUiLCJpZCI6Mzk5MSwic2NvcGVzIjpbImFzbCIsImFzciIsImFzdyIsImdjIl0sImlhdCI6MTUzOTU3OTE2NX0.-25udUzENRJ66mnICMK8Hfc6xgF_VP7P4sWkSHaUjOQ';
        let viewer = new Cesium.Viewer('cesiumContainer', {
            geocoder: false,//是否显示geocoder小器件，右上角查询按钮
            homeButton:false,//是否显示Home按钮
            sceneModePicker:false,//是否显示3D/2D选择器
            baseLayerPicker:false,//是否显示图层选择器
            navigationHelpButton:false,//是否显示右上角的帮助按钮
            animation:false,//是否创建动画小器件，左下角仪表
            creditContainer:"cesiumContainer",// 对应上面div的ID
            timeline:false,//是否显示时间轴
            fullscreenButtion:false,//是否显示全屏按钮
            vrButton:false,
            selectionIndicator : false,//是否显示选取指示器组件
        });
        // 上面配置选项可以控制部分控件是否显示
        viewer.scene.debugShowFramesPerSecond = true;// 显示帧率
        /** 默认使用bing地图，转换为google地图开始 **/
        let google = new Cesium.UrlTemplateImageryProvider({
            url : 'https://mt0.google.cn/vt/lyrs=s&hl=zh-CN&x={x}&y={y}&z={z}',
            tilingScheme : new Cesium.WebMercatorTilingScheme(),
            maximumLevel : 20
        });
        viewer.imageryLayers.addImageryProvider(google);
        /** 默认使用bing地图，转换为google地图结束 **/

        this.viewer = viewer

        //中阻止镜头飞至地表以下
        var minPitch = -Cesium.Math.PI_OVER_TWO;
        var maxPitch = 0;
        var minHeight = 200;
        viewer.camera.changed.addEventListener(
            function() {
                if (viewer.camera._suspendTerrainAdjustment && viewer.scene.mode === Cesium.SceneMode.SCENE3D) {
                    viewer.camera._suspendTerrainAdjustment = false;
                    viewer.camera._adjustHeightForTerrain();
                }
                
                // Keep camera in a reasonable pitch range
                var pitch = viewer.camera.pitch;

                if (pitch > maxPitch || pitch < minPitch) {
                    viewer.scene.screenSpaceCameraController.enableTilt = false;

                    // clamp the pitch
                    if(pitch > maxPitch ) { 
                        pitch = maxPitch; 
                    } else if(pitch < minPitch) {
                        pitch = minPitch;
                    }

                    var destination = Cesium.Cartesian3.fromRadians(
                        viewer.camera.positionCartographic.longitude,
                        viewer.camera.positionCartographic.latitude,
                        Math.max(viewer.camera.positionCartographic.height, minHeight));

                    viewer.camera.setView({
                        destination: destination,
                        orientation: { pitch: pitch }
                    });
                    viewer.scene.screenSpaceCameraController.enableTilt = true;
                }
            }
        );

        //重置home按钮跳转
        // viewer.homeButton.viewModel.command.beforeExecute.addEventListener(function(e) {
		// 	e.cancel = true;
        //     //你要飞的位置	
        //     if(loactionTectEntity)
        //         viewer.entities.remove(entity);
        //     let loactionTectEntity = viewer.entities.add({
        //         position: Cesium.Cartesian3.fromDegrees(114.367932, 30.609873),
        //         point : {
        //             pixelSize : 1,
        //             color : Cesium.Color.WHITE.withAlpha(0.1),
        //             outlineColor : Cesium.Color.WHITE.withAlpha(0.1),
        //             outlineWidth : 1
        //         }
        //     });
        //     viewer.flyTo(loactionTectEntity, {
        //         duration: 2,
        //         offset : {
        //             heading : Cesium.Math.toRadians(0.0),
        //             pitch : Cesium.Math.toRadians(-20),
        //             range : 50000
        //         }
        //     });
        // });
        

    let scene = this.viewer.scene;

var canvas = viewer.canvas;
canvas.setAttribute('tabindex', '0'); // 将焦点放在画布上
//如果点击就获取画布焦点
canvas.addEventListener('click', function() {
    canvas.focus();
});
canvas.focus();
 
//飞行路径的位置
var pathPosition = new Cesium.SampledPositionProperty();
//添加飞机飞行路径
var entityPath = viewer.entities.add({
    position : pathPosition,
    name : 'path',
    path : {
        show : true,
        leadTime : 0,
        trailTime : 60,
        width : 10,
        resolution : 1,
        material : new Cesium.PolylineGlowMaterialProperty({
            glowPower : 0.3,
            color : Cesium.Color.PALEGOLDENROD
        })
    }
});
 
var camera = viewer.camera;
var controller = scene.screenSpaceCameraController;
var r = 0;
//中心点
var center = new Cesium.Cartesian3();
 
//机身模型的偏移参数
var hpRoll = new Cesium.HeadingPitchRoll();
//相机模型的偏移参数
var hpRange = new Cesium.HeadingPitchRange();
 
var speed = 50;
//默认按一下偏移3度
var deltaRadians = Cesium.Math.toRadians(3.0);
 
//飞机位置
var position = Cesium.Cartesian3.fromDegrees(-123.0744619, 44.0503706, 5000.0);
//速度向量
var speedVector = new Cesium.Cartesian3();
//生成一个由两个参考系生成的矩阵
var fixedFrameTransform = Cesium.Transforms.localFrameToFixedFrameGenerator('north', 'west');
 
//添加模型
var planePrimitive = scene.primitives.add(Cesium.Model.fromGltf({
    //这里需要把模型路径改下(如果你用的还是HelloWord.html的话就用这个,不是的话请自行修改)
    url : 'static/lib/Cesium_Air.gltf',
    modelMatrix : Cesium.Transforms.headingPitchRollToFixedFrame(position, hpRoll, Cesium.Ellipsoid.WGS84, fixedFrameTransform),
    minimumPixelSize : 228
}));
 
viewer.camera.flyTo({
    destination: Cesium.Cartesian3.fromDegrees(-123.0744619, 44.0503706,),
    duration: 3,
    complete: function () {
        //动画播放
        // setTimeout(function(){ 
            planePrimitive.readyPromise.then(function(model) {
                // 以半速循环动画
                model.activeAnimations.addAll({
                    speedup : 0.5,
                    loop : Cesium.ModelAnimationLoop.REPEAT
                });
            
                //r=2*max(模型的半径，相机的最近距离)
                r = 2.0 * Math.max(model.boundingSphere.radius, camera.frustum.near);
                //镜头最近距离
                controller.minimumZoomDistance = r * 0.5;
                //计算center位置(也为下面的镜头跟随提供了center位置)
                Cesium.Matrix4.multiplyByPoint(model.modelMatrix, model.boundingSphere.center, center);
                //相机偏移角度
                var heading = Cesium.Math.toRadians(230.0);
                var pitch = Cesium.Math.toRadians(-20.0);
                hpRange.heading = heading;
                hpRange.pitch = pitch;
                hpRange.range = r * 50.0;
                //固定相机
                camera.lookAt(center, hpRange);
            });
        // }, 3000);
    }
});


 
//键盘监听
document.addEventListener('keydown', function(e) {
    switch (e.keyCode) {
        case 40:
            if (e.shiftKey) {
                // 按住shift加下箭头减速
                speed = Math.max(--speed, 1);
            } 
            // else {
            //     // 直接按下箭头降低角度
            //     hpRoll.pitch -= deltaRadians;
            //     if (hpRoll.pitch < -Cesium.Math.TWO_PI) {
            //         hpRoll.pitch += Cesium.Math.TWO_PI;
            //     }
            // }
            break;
        case 38:
            if (e.shiftKey) {
                // 按住shift加上箭头加速
                speed = Math.min(++speed, 500);
                if(speed == 300){
                    alert("你已超速，请注意安全！")
                }
                if(speed == 500){
                    alert("为保证您的安全，速度已到上限！")
                }
            } 
            // else {
            //     // 直接按上箭头抬高角度
            //     hpRoll.pitch += deltaRadians;
            //     if (hpRoll.pitch > Cesium.Math.TWO_PI) {
            //         hpRoll.pitch -= Cesium.Math.TWO_PI;
            //     }
            // }
            break;
        case 39:
            if (e.shiftKey) {
                // 飞机本身向右旋转
                hpRoll.roll += deltaRadians;
                if (hpRoll.roll > Cesium.Math.TWO_PI) {
                    hpRoll.roll -= Cesium.Math.TWO_PI;
                }
            } else {
                // 向右飞行
                hpRoll.heading += deltaRadians;
                if (hpRoll.heading > Cesium.Math.TWO_PI) {
                    hpRoll.heading -= Cesium.Math.TWO_PI;
                }
            }
            break;
        case 37:
            if (e.shiftKey) {
                // 飞机本身向左旋转
                hpRoll.roll -= deltaRadians;
                if (hpRoll.roll < 0.0) {
                    hpRoll.roll += Cesium.Math.TWO_PI;
                }
            } else {
                // 向左飞行
                hpRoll.heading -= deltaRadians;
                if (hpRoll.heading < 0.0) {
                    hpRoll.heading += Cesium.Math.TWO_PI;
                }
            }
            break;
        default:
    }
});
 
var headingSpan = document.getElementById('heading');
var pitchSpan = document.getElementById('pitch');
var rollSpan = document.getElementById('roll');
var speedSpan = document.getElementById('speed');
var fromBehind = document.getElementById('fromBehind');
 
//给左边的通知栏更新数据同时刷新飞机位置(这里也是个1ms一次的回调)
viewer.scene.preRender.addEventListener(function(scene, time) {
    headingSpan.innerHTML = Cesium.Math.toDegrees(hpRoll.heading).toFixed(1);
    pitchSpan.innerHTML = Cesium.Math.toDegrees(hpRoll.pitch).toFixed(1);
    rollSpan.innerHTML = Cesium.Math.toDegrees(hpRoll.roll).toFixed(1);
    speedSpan.innerHTML = speed.toFixed(1);
 
    //选择的笛卡尔分量Cartesian3.UNIT_X（x轴单位长度）乘以一个标量speed/10，得到速度向量speedVector
    speedVector = Cesium.Cartesian3.multiplyByScalar(Cesium.Cartesian3.UNIT_X, speed / 10, speedVector);
    //飞机的模型矩阵与速度向量speedVector相乘，得到position
    position = Cesium.Matrix4.multiplyByPoint(planePrimitive.modelMatrix, speedVector, position);
    //添加一个路径模型(就是白色的尾气)
    pathPosition.addSample(Cesium.JulianDate.now(), position);
    //飞机位置+旋转角度+地球+坐标矩阵=飞机模型矩阵
    Cesium.Transforms.headingPitchRollToFixedFrame(position, hpRoll, Cesium.Ellipsoid.WGS84, fixedFrameTransform, planePrimitive.modelMatrix);
 
    if (fromBehind.checked) {
        // 镜头跟随
        Cesium.Matrix4.multiplyByPoint(planePrimitive.modelMatrix, planePrimitive.boundingSphere.center, center);
        // hpRange.heading = hpRoll.heading;
        // hpRange.pitch = hpRoll.pitch;
            var heading = Cesium.Math.toRadians(230.0);
    var pitch = Cesium.Math.toRadians(-20.0);
    hpRange.heading = heading;
    hpRange.pitch = pitch;
    hpRange.range = r * 50.0;
        camera.lookAt(center, hpRange);
    }
});
        this.open2();
    },
    methods:{
        open2() {
            this.$notify({
                title: '友情提示',
                message: '有问题，向阿锦反馈843100406@qq.com',
                position: 'bottom-right',
                duration: 0
            });
        },
        open() {
            this.$confirm('你已超速，请注意安全！', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            })
        }

    },
    components: {

    }

}
</script>

<style lang="scss" scoped>
html,
body,
#cesiumContainer {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.cesium-widget-credits{ display:none} /**  隐藏版权信息 **/
.cesium-viewer .cesium-widget-credits{ display:none }
.textboxMain{
    z-index: 100000;
    padding: 10px;
    width:140px;
    background: url("../../assets/images/yqimagebg5.png") no-repeat center;
    background-size: 100% 100%; 
    height: auto;
    position: absolute;
    top: 20px;
    left:20px;
    color: #eee;
    font-size: 14px;
}
.textboxMain .divH{
    margin: 5px 0;
}
.textboxMain .divHh{
    margin-top: 20px;
    color: #ccc;
}
.textboxMain .divHh h3{
    padding: 10px 0;
}
.textboxMain .divHh p{
    margin: 5px 0;
    line-height: 1.3;
}
</style>



